import React from 'react';
import {connect} from "react-redux";
import {toggleTab} from "src/actions/uiActions";
import store from 'src/store';

const Tab = (props) => {
    let stateData = store.getState();
    let tabGroupData=(stateData?.tabGroups.hasOwnProperty(props.groupName) ? stateData.tabGroups[props.groupName] : {});
    let groupClass = props.isActiveTab ? tabGroupData.groupTabActiveClassName  : tabGroupData.groupTabClassName
    let propsClassName = props.tabClass ? props.tabClass : "";
    let activeClass = props.isActiveTab ? "active" : "";

    return (
            <li className={"nav-item kimTab " + groupClass + " " + propsClassName} style={Object.assign({},tabGroupData.groupTabStyle,props.tabStyle)}
                 onClick={()=>{store.dispatch(toggleTab(props.groupName,props.tabKey))}}>
                    <a className={"nav-link kimTabLabel " + activeClass}>{props.label}</a>
            </li>
        )
};

const mapStateToProps = (state) =>{
    return {
        tabGroups:state.tabGroups
    };
};

export default connect(mapStateToProps,{})(Tab);
